body {
  margin: 0;
  padding: 0;
  font-family: "Inter", sans-serif;
  font-family: "Inter", system-ui, sans-serif;
  background: radial-gradient(circle at top, #020617, #000);
  color: #e5e7eb;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 5%; /* Flexible padding */
  position: sticky;
  top: 0;
  background: rgba(0,0,0,0.85);
  backdrop-filter: blur(10px);
  z-index: 1000;
  flex-wrap: wrap; /* Allows items to drop to next line if needed */
}

.navbar a {
  margin: 5px 10px; /* Uniform spacing instead of just left margin */
  color: #38bdf8;
  font-weight: 500;
  white-space: nowrap; /* Prevents text from breaking mid-word */
}

@media (max-width: 600px) {
  .navbar {
    justify-content: center; /* Centers the logo and links on small phones */
    padding: 10px;
  }
  
  .navbar a {
    font-size: 0.85rem; /* Slightly smaller text for mobile */
    margin: 5px;
  }
}


.navbar a {
  margin-left: 20px;
  color: #38bdf8;
  font-weight: 500;
}

.hero {
  text-align: center;
  padding: 120px 20px;
}

.hero h1 {
  font-size: 3.5rem;
}

.button {
  display: inline-block;
  margin-top: 20px;
  padding: 12px 24px;
  border: 1px solid #38bdf8;
  color: #38bdf8;
  border-radius: 6px;
}

section {
  max-width: 1000px;
  margin: auto;
  padding: 80px 20px;
}

.card img {
  width: 100%;
  border-radius: 10px;
  margin-top: 15px;
}

/* Add a media query for mobile devices */
@media (max-width: 768px) {
  .navbar {
    flex-direction: column; /* Stack logo and links */
    padding: 15px;
    gap: 10px;
  }

  .navbar a {
    margin-left: 10px; /* Reduce spacing between links */
    margin-right: 10px;
    font-size: 0.9rem;
  }

  .hero {
    padding: 60px 20px; /* Reduce large desktop padding */
  }

  .hero h1 {
    font-size: 2.2rem; /* Shrink title to prevent overflow */
  }
}
.gallery-grid {
  display: grid;
  /* Automatically fits as many columns as possible (min 250px each) */
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 20px;
  margin-top: 30px;
}

.gallery-item {
  background: #262626;
  padding: 10px;
  border-radius: 12px;
  text-align: center;
}

.gallery-item img {
  width: 100%;
  height: 200px;
  object-fit: cover; /* Prevents image distortion */
  border-radius: 8px;
}

/* Mobile Menu Styles */
@media (max-width: 768px) {
  .menu-icon {
    display: flex;
    flex-direction: column;
    gap: 5px;
    cursor: pointer;
  }

  .bar {
    width: 25px;
    height: 3px;
    background-color: #38bdf8;
    transition: 0.3s;
  }

  .nav-links {
    display: none; /* Hide links by default on mobile */
    width: 100%;
    flex-direction: column;
    text-align: center;
    padding-top: 20px;
  }

  .nav-links.active {
    display: flex; /* Show when 'active' class is added */
  }
}

@media (min-width: 769px) {
  .menu-icon {
    display: none; /* Hide hamburger on desktop */
  }
  .nav-links {
    display: flex;
  }
}
/* Hide menu icon on desktop */
.menu-icon {
  display: none;
  font-size: 1.5rem;
  color: white;
  cursor: pointer;
}

@media (max-width: 900px) {
  .menu-icon {
    display: block;
    z-index: 1001;
  }

  /* Force the links container to become a mobile dropdown */
  .nav-links {
    display: none !important; /* Override the inline 'flex' style */
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    background: #1a2a6c;
    flex-direction: column;
    padding: 20px;
    gap: 15px !important;
    border-bottom: 2px solid #61dafb;
  }

  .nav-links.active {
    display: flex !important;
  }

  .nav-divider {
    display: none; /* Hide the vertical line on mobile */
  }

  /* Shrink text for smaller screens */
  .logo {
    font-size: 1rem !important;
  }
}
